.weui-slider {
  padding: 15px 18px;
  user-select: none;
}
.weui-slider__inner {
  position: relative;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.1);
}
.weui-slider__track {
  height: 2px;
  background-color: #07C160;
  width: 0;
}
.weui-slider__handler {
  position: absolute;
  left: 0;
  top: 50%;
  width: 28px;
  height: 28px;
  margin-left: -14px;
  margin-top: -14px;
  border-radius: 50%;
  background-color: #FFFFFF;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}
.weui-slider-box {
  display: flex;
  align-items: center;
}
.weui-slider-box .weui-slider {
  flex: 1;
}
.weui-slider-box__value {
  margin-left: .5em;
  min-width: 24px;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
  font-size: 14px;
}
